import { atom, map, computed } from "nanostores";
export const $pageInfo = map({
    url: "",
    title: "",
    description: "",
    tldr: "",
    synopsis: "",
    notes: "",
    tags: [],
});
export const $settings = map({
    serverUrl: "http://localhost:3000",
    MEMAI_API_KEY: "",
    OPENAI_API_KEY: "",
});
export const $activePanel = atom("PageInfo");
// export const activePanel = atom<"PageInfo" | "Settings">("Settings");
export const $allTags = computed($pageInfo, ({ tags, noteTags }) => {
    return [...new Set([...(tags ?? []), ...(noteTags ?? [])])];
});
